.btn-theme{
  cursor: pointer;
}

.navbar{
  padding-top: 35px;
  padding-bottom: 35px;
  background: var(--background-color-nav);
}

.logo{
  width: 60px;
  height: 60px;
}

.section-items{
  margin-bottom: 25px;
}


  .cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 360px) minmax(300px, 360px) minmax(300px, 360px));
    grid-gap: 20px 20px;
  }

@media (min-width: 768px) and (max-width: 1023px) {
  .cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(340px, auto) minmax(340px, auto));
    grid-gap: 20px 20px;
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  .cards{
    margin-left: 20px;
    margin-right: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, auto));
    grid-gap: 20px 20px;
  }
}

@media (min-width: 100px) and (max-width: 768px) {
  .select-box {
      max-width: 100%!important;
  }
}

.row-items{
  display: grid;
  grid-template-columns:  minmax(160px, 265px) minmax(160px, 265px) minmax(160px, 265px) minmax(160px, 265px);
  grid-gap: 0px 20px;
}

@media (min-width: 320px) and (max-width: 767px) {
  .row-items{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, auto));
    grid-template-rows: 45px 45px 45px 45px;
    grid-gap: 15px 20px;
  }
}

.cart{
  max-height: 275px;
  min-height: 205px;
  background: aliceblue;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.cart:hover .container-painting-rules{
  height: 55%;
  transition: .5s;
}

.cart:hover .description{
  white-space: normal;
}

.img-paint {
  width: 100%!important;
  position: relative;
  height: 100%;
  border-radius: 20px;
  position: relative;
}

.container-painting-rules{
    height: 12%;
    text-transform: capitalize;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 20px;
    position: absolute;
    bottom: 0;
    color: black;
    padding: 5px 0px 15px 15px;
    background: #ffffffbf;
    width: 100%;
    border-radius: 0px 0px 20px 20px;
    transition: .5s;
}

.description{
  margin-bottom: 6px;
  white-space: nowrap;
  text-overflow: ellipsis;
  position: relative;
  order: 0;
  overflow: hidden;
}

.rules{
  font-size: 13px;
  line-height: 20px;
  margin-bottom: 5px;
}

.rules-span{
  font-weight: 300;
  font-size: 13px;
  line-height: 20px;
}

.btns-numbers-pages {
  display: flex;
  position: absolute;
  left: 80px;
}

.paginations {
    display: flex;
    width: 280px;
    height: 41px;
    position: relative;
}

.paginations-items{
  margin-top: 40px;
  padding-bottom: 40px;
}

.btn-back{
  position: absolute;
  left: 0;
  z-index: 1;
}

.btn-next{
  position: absolute;
  right: 0;
  z-index: 1;
}

.btn-page{
  width: 40px;
  height: 40px;
}

.btn-nav{
  width: 40px;
  height: 40px;
}

.btn-paginations{
  color: var(--color-nav-texts);
  border: 1px solid var(--color-nav-border);
  border-right: 0px;
  background: var(--color-nav-background);
  font-size: 18px;
}

.btn-paginations-nav-total{
  border-right: 1px solid var(--color-nav-border);
}

.total-right{
  border-radius: 0px 10px 10px 0px;
}

.total-left{
  border-radius: 10px 0px 0px 10px;
}

.active-btn-page {
  background: var(--color-nav);
  color: var(--color-nav-text);
}

.disabled{
  color: #b7b7b7;
  border-color: #b7b7b7;
}

.active-select-box-backdrop{
  z-index: 1;
}

.pNull{
